var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('v-layout',{attrs:{"justify-center":""}},[_c('v-flex',{attrs:{"xs11":"","md12":"","lg10":"","xl8":""}},[_c('v-card',{staticClass:"subheading mx-3 px-3 transparent",attrs:{"flat":""}},[_c('v-card-title',{staticClass:"headline white--text"},[_vm._v("Pour pouvoir avoir un regard critique sur la production éléctrique, il faut commencer par comprendre\r\n          ce qu'est réelment un moyen de production éléctrique, nous allons commencer par étudier le principe sur lequel ils fonctionnent.\r\n        ")])],1),_c('v-card',{staticClass:"subheading mx-5 mt-4 pa-3"},[_c('v-card-text',[_c('v-card-title',{staticClass:"title"},[_vm._v("L'induction")]),_c('v-card',{staticClass:"px-3",attrs:{"flat":""}},[_vm._v("Il y a plusieurs façon de produire de l'éléctricité mais nous n'étuedierons ici que l'induction, phénomène découvert le 29 août 1831 par le chimiste Britanique Michael Faraday (1791-1867) car la trés grande majorité des moyens de productions éléctrique\r\n            fonctionnent grâce à ce principe.\r\n          ")]),_c('v-card',{staticClass:"mt-1 pa-3",attrs:{"flat":""}},[_vm._v("Essayez de déplacer l'aimant à l'intérieur de la bobine de cuivre (vue en coupe), bobine dont les extémités ont été reliés aux bornes d'une lampe.\r\n          ")]),_c('Lampe'),_c('v-card',{staticClass:"mt-2 pa-3",attrs:{"flat":""}},[_vm._v("En déplaçant l’aimant, des charges vont être attirées, d’autres repoussées. Les électrons libres du fil vont bouger : ce mouvement d’électrons est par définition un courant électrique. Sans le mouvement de l’aimant, les électrons se placent dans une position\r\n            d’équilibre et ne bougent plus. Plus la vitesse de déplacement de l’aimant par rapport à la bobine est grande, plus les électrons du fil métallique seront perturbés, plus le courant sera important. Ce phénomène s’appelle l’induction.\r\n          ")]),_c('v-card',{staticClass:"mt-2 pa-3",attrs:{"flat":""}},[_vm._v("D’autres caractéristiques vont jouer également comme la taille de l’aimant, ou le nombre de tours de fil sur la bobine :\r\n            "),_c('v-card',{staticClass:"pl-3",attrs:{"flat":""}},[_vm._v("Un aimant plus gros est plus « puissant » et crée un champ magnétique plus intense : un plus grand nombre d’électrons est perturbé.\r\n            ")]),_c('v-card',{staticClass:"pl-3",attrs:{"flat":""}},[_vm._v("Plus la bobine comporte de tours, plus l’aimant qui se déplace va agir sur un grand nombre d’électrons en même temps : l’intensité du courant électrique (le 'débit' d’électrons) sera plus importante.\r\n            ")])],1)],1)],1),_c('v-card',{staticClass:"subheading ma-5 pa-3"},[_c('v-card-text',[_c('v-card-title',{staticClass:"title"},[_vm._v("L'alternateur")]),_c('v-card',{staticClass:"mt-1 px-3",attrs:{"flat":""}},[_vm._v("En 1831, donc la même année que la découverte de l'induction, Hippolyte Pixii (1808 - 1835), fabricant d'instruments de Paris, sur les indications d'André-Marie Ampère, mathématicien, physicien, chimiste et philosophe français, construit le premier alternateur.\r\n          ")]),_c('v-card',{staticClass:"mt-2 px-3",attrs:{"flat":""}},[_vm._v("Un alternateur est une machine rotative qui convertit l'énergie mécanique fournie au rotor en énergie électrique à courant alternatif,\r\n          alternatif car les éléctrons sont alternativement atirés et repoussés par l'aimant.\r\n          ")]),_c('v-card',{staticClass:"mt-2 px-3",attrs:{"flat":""}},[_vm._v("Plus de 95 % de l’énergie électrique est produite par les alternateurs, ce sont des machines électromécaniques fournissant des tensions alternatives de fréquence proportionnelle à leur vitesse de rotation. Ces machines sont peu coûteuses par raport à\r\n            d'autre moyens de production éléctrique et ont un rendement de l'ordre de 95 %.\r\n          ")]),_c('v-card',{staticClass:"mt-2 pa-3 pb-0 title",attrs:{"flat":""}},[_vm._v("Principe de l